import { email, srConfig } from '@config';
import { usePrefersReducedMotion } from '@hooks';
import sr from '@utils/sr';
import React, { useEffect, useRef } from 'react';
import styled from 'styled-components';

const StyledContactSection = styled.section`
  max-width: 600px;
  margin: 0 auto 100px;
  text-align: center;

  @media (max-width: 768px) {
    margin: 0 auto 50px;
  }

  .overline {
    display: block;
    margin-bottom: 20px;
    color: var(--green);
    font-family: var(--font-mono);
    font-size: var(--fz-md);
    font-weight: 400;

    &:before {
      bottom: 0;
      font-size: var(--fz-sm);
    }

    &:after {
      display: none;
    }
  }

  .title {
    font-size: clamp(40px, 5vw, 60px);
  }

  .email-link {
    ${({ theme }) => theme.mixins.bigButton};
    margin-top: 50px;
  }
`;


function onEmailClick() {
  window.open(`mailto:${email}`);
}

const Contact = () => {
  console.log(email);
  const revealContainer = useRef(null);
  const prefersReducedMotion = usePrefersReducedMotion();

  useEffect(() => {
    if (prefersReducedMotion) {
      return;
    }

    sr.reveal(revealContainer.current, srConfig());
  }, []);

  return (
    <StyledContactSection id="contact" ref={revealContainer}>
      <h2 className="numbered-heading overline">Whats Next?</h2>

      <h2 className="title">Get In Touch</h2>

      <p>
        Although Im not currently looking for any new opportunities, my inbox is always open.
        Whether you have a question or just want to say hi, Ill try my best to get back to you!
      </p>

      <a className="email-link" target={"_blank"} href="mailto:pruthvisoni7@gmail.com">
        Say Hello
      </a>

    </StyledContactSection>
  );
};

export default Contact;
